* {
  box-sizing: border-box;
}

.page__body {
  font-family: "Nunito", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  background: url("../img/svg/bg.svg"), linear-gradient(174.2deg, rgb(255, 244, 228) 7.1%, rgb(240, 246, 238) 67.4%);
  background-size: 280px auto, cover;
  background-attachment: fixed;
}

.container {
  height: 100%;
  max-width: 1200px;
  padding: 0 20px;

  @include desktops {
    width: 100%;
  }
}

img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.wrapper {
  background-color: $body-bg;
  box-shadow: 10px 0 19px -17px #000000, -10px 0 19px -17px #000000;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  margin: 0 auto;
}

body,
html {
  height: 100%;
  // background-color: $white;
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $black;
  margin: 0;
}

a {
  text-decoration: none;
  cursor: pointer;
}


.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
